  display: block;
  max-width: 100%;
  height: auto;
  margin: auto;
  -webkit-filter: grayscale(100%) blur(5px);
  filter: grayscale(100%) blur(5px);
} */

#header-text {
  color: black;
  font-size: 35px;
  font-size: 7vw;
  font-weight: 400;
  display: flex;
  z-index: 999;
  left: 0;
  right: 0;
  top: 15vh;
  max-width: 90%;
  text-align: left;
}

a {
  color: #a50000;
  text-decoration: none;
}

.link-text {
  color: black;
  text-decoration: none;
}

a:hover {
  text-decoration: underline;
}

#header {
  color: black;
}

#portfolio h2 {
  margin-top: 100px;
  text-align: left;
  font-size: 4vw;
}

ul {
  text-align: left;
  list-style-type: none;
  padding-inline-start: 0px;
}

li {
  padding: 40px 0px;
  font-size: 4.5vw;
}

.border-top {
  border-top: 1px solid grey;
}

.border-bottom {
  border-bottom: 1px solid grey;
}

i {
  padding: 10px;
}

#contact h2 {
  color: black;
  font-size: 4vw;
  text-align: left;
  font-weight: 400;
  margin-top: 100px;
  font-weight: 400
}

#links {
  flex-direction: column;
  text-align: center;
  font-size: 2vw;
  text-decoration: none;
  letter-spacing: 5px;
  text-transform: uppercase;
  padding: 0px 0px 20px 0px;
}

#main {
  text-align: center;
  color: black;
  padding-top: 20px;
  border: none;
}

@media screen and (min-width: 768px) {

  #contact h2 {
    font-size: 2vw;
  }

  #header-text {
    color: black;
    font-size: 35px;
    font-size: 4vw;
    font-weight: 400;
    display: flex;
    z-index: 999;
    left: 0;
    right: 0;
    top: 15vh;
    max-width: 90%;
    text-align: left;
  }

  #portfolio h2 {
    margin-top: 100px;
    text-align: left;
    font-size: 2vw;
  }

  li {
    padding: 40px 0px;
    font-size: 2.5vw;
  }

  #contact h2 {
    font-size: 2vw;
  }

}